<template>
    <div>
        <img src="../images/tick.svg" style="display: none" class="tick-image" alt="Save">
        <br>
        {{ item.name }}
        <br><br>
        <button @click="turnInput = !turnInput">{{ btnName }}</button>
        <br><br>
        <div v-if="turnInput">
            <input type="text" placeholder="ToDo Name" v-model="recordInput">
            <button @click="newRecord" :disabled="recordInput == false ? true : false" class="add-btn">Add ToDo</button>     
        </div>
        <ul>
            <li v-for="(recordObj, index) in item.records" :key="recordObj.record" :class="'record-' + index">
                <div class="rec-content">
                    <input type="checkbox" 
                    v-model="recordObj.checked" 
                    class="note-checkbox"
                    :value="recordObj.record">
                    {{ recordObj.record }}
                    <img class="btn-img" style="height: 1.1em" title="Edit" @click="editRecord(index)" src="../images/edit.svg">
                    <img class="btn-img" style="height: 1.1em" title="Delete" @click="popup(index)" src="../images/close.svg">
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
import { mapActions } from 'vuex'
export default {
    props: ['note'],
    data() {
        return {
            recordInput: '',
            turnInput: false
        }
    },
    computed: {
        item() {
            return this.$store.state.list[this.note]
        },
        btnName() {
            return this.turnInput ? 'Cancel New ToDo' : 'Add ToDo'
        },
    },
    methods: {
        // Calling popup
        popup(index) {
            const question = 'Delete Todo?'
            this.popUp({question, index, note: this.note})
        },
        ...mapMutations(['addRecord']),
        ...mapActions(['saveState', 'popUp']),
        // Creating New Todo
        newRecord() {
            this.saveState();
            this.addRecord([this.note, this.recordInput])
            this.recordInput = ''
        },
        createInput(index, val) {
            const input = document.createElement('input')
            input.setAttribute('value', val)
            input.classList.add('input-' + index)
            return input         
        },
        createBtn(index) {
            const saveBtn = document.querySelector('.tick-image').cloneNode()
            saveBtn.classList.add('save-btn')
            saveBtn.style.display = 'inline-block'
            saveBtn.setAttribute('data-index', index)
            saveBtn.setAttribute('title', 'Save')
            return saveBtn
        },
        // Creating field for editing name
        startEdit(index, saveBtn, input) {
            const record = document.querySelector(`.record-${index}`)
            record.querySelector(`.rec-content`).style.display = 'none'
            record.prepend(saveBtn)
            record.prepend(input)
        },
        editRecord(index) {
            // Creating elements for editing field 
            const val = this.item.records[index].record
            const input = this.createInput(index, val)
            const saveBtn = this.createBtn(index)
            this.startEdit(index, saveBtn, input)
            saveBtn.addEventListener('click', this.updRecord)  // Button for save editing
        },
        // Updating ToDo name
        updRecord(e) {
            this.saveState();
            const index = e.target.getAttribute('data-index')
            if(this.item.records[index].record === document.querySelector('.input-' + index).value) this.stopEdit(index)            
            else this.item.records[index].record = document.querySelector('.input-' + index).value
        },
        // Closing editing field
        stopEdit(index) {
            const record = document.querySelector(`.record-${index}`)
            record.querySelector(`.rec-content`).style.display = 'block'
            record.removeChild(record.querySelector('.input-' + index))
            record.removeChild(record.querySelector('.save-btn'))
        },
        // Deleting Todo 
        delRecord(index) {
            this.saveState();
            this.$store.state.list[this.note].records.splice(index, 1)
        }
    }
}
</script>

<style scoped>
    button {
        font-weight: bold;
        cursor: pointer;
        padding: 8px;
        transition-duration: .4s;
        border-radius: 5px;
    }
    input {
        padding: 7px;
        border: 1px solid grey;
        border-radius: 5px 0 0 5px;
    }
    .add-btn {
        background: #efefef;
        border: 0px;
        border-radius: 0 5px 5px 0;
    }
    .add-btn:hover {
        background: dimgray;
        color: #fff;
    }
    .save-btn {
        display: inline-block;
        height: 1.1em;
        margin-left: .9em;
        cursor: pointer;
    }
    .btn-img {
        margin-left: 15px;
        cursor: pointer;
    }
    ul {
        list-style-type: none;
    }
    li {
        border: 1px solid #cecece;
        padding: 8px;
        border-radius: 5px;
    }
    .rec-content {
        display: flex;
        align-items: center;
    }
    .note-checkbox {
        margin-right: 10px;
    }
</style>