<template>
    <div>
        <div class="popup" v-show="popup">
            <p>{{ question }}</p>
            <div>
                <button @click="delNote(index)" class="true-btn">Yes</button>
                <button @click="hide" class="false-btn">No</button>        
            </div>
        </div>   
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
export default {
    computed: {
        ...mapGetters(['question', 'popup', 'index'])
    },
    methods: {
        ...mapActions(['delNote', 'hide']),
    }
}
</script>
